$white: rgba(255, 255, 255, 1);
$black: rgba(0, 0, 0, 1);

$text-color: rgba(23, 43, 76, 1);
$bg-color: $white;
$link-color: rgba(3, 102, 214, 1);
$faded-link-color: rgba(119, 119, 119, 1);
$contrast-color: rgba(160, 0, 0, 1);
$faded-color: rgba($black, 0.5);
$border-color: rgba(221, 221, 221, 1);

$body-font: -apple-system-font, BlinkMacSystemFont, Helvetica, Helvetica Neue, Segoe UI, Roboto,
  Arial, sans-serif;
$code-font: SFMono-Regular, Consolas, Liberation Mono, Menlo, Courier, monospace;

$post-content-width: 100%;
$mobile-width: 760px;
$inline-notes-width: 1300px;
